<template>
  <div class="address">
    <header class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span v-if="type === '0'">设置提现密码</span>
      <span v-if="type ==='1'">修改提现密码</span>
    </header>
    <div class="site-box">
      <div class="site">
          <span>
            手机号
          </span>
          <p>{{data.tel}}</p>
      </div>
      <label class="code site">
        <a href="javascript:void(0)" @click="getCode" v-if="flag">获取验证码</a>
        <a href="javascript:void(0)"  v-if="!flag">{{time}}秒</a>
        <input type="text" class="input" placeholder="请输入验证码" v-model.trim="code" @blur="check">
      </label>
      <label class="site">
          <span>
            支付密码
          </span>
        <input type="password" placeholder="请输入支付密码" v-model.trim="password" @blur="check">
      </label>
      <label class="site">
          <span>

          </span>
        <input type="password" placeholder="请再次支付密码" v-model.trim="password1" @blur="check">
      </label>
    </div>
    <footer class="footer_btn" @click="save" v-if="btn">
      {{content}}
    </footer>
    <footer class="footer_btn footer_d"  v-if="!btn">
      {{content}}
    </footer>
  </div>
</template>

<script>
  import { Toast,MessageBox,Indicator} from 'mint-ui';
  export default {
    data () {
      return {
        content:'设置',
        flag: true,
        code:'',
        type:'',
        password:'',
        password1: '',
        time: 60,
        data:{},
        btn:false
      }
    },
    methods: {
      getCode(){
        Indicator.open();
        //倒计时
        this.flag = false;
        const timer = setInterval(() => {
          this.time --;
          if(this.time === 0) {
            this.flag = true;
            this.time = 60;
            clearInterval(timer);
          }
        }, 1000);
        this.post('home/Withdraw/sendMsg',{
          tel: this.data.tel
        }).then(res => {
          Indicator.close();
          if (res.code === 10000){
            Toast(res.data.msg)
          } else Toast(res.data.msg)
        })
      },
      check() {
        this.btn = this.code && this.password && this.password1;
      },
      //设置密码
      setPass(url){
        Indicator.open();
        this.post(url, {
          msgCode: this.code,
          password: this.password,
          password1: this.password1
        }).then(res => {
          Indicator.close();
          if (res.data.code === 10000) {
            Toast(res.data.msg);
            this.$router.push('/wallet')
          } else {
            Toast(res.data.msg)
          }
        })
      },
      save(){
          if (this.type === '1') {
              this.setPass('home/Withdraw/updateTxPassword')
          } else {
              this.setPass('home/Withdraw/setTxPassword')
          }
      },
      back(){
        this.$router.go(-1);
      },
    },
    mounted(){
      this.type = this.$route.params.type;
      this.type === '1' ? this.content = '修改': this.content = '设置'
      this.post('home/Withdraw/getWithdrawInfo', {}).then(res => {
        if (res.data.code === 10000) {
          this.data = res.data.info;
        }
      })
    },
  }
</script>

<style scoped>
  @import "../../../static/header.css";
  .code {
    position: relative;
  }
  .code>input {
    padding-left: 1.4rem;
  }
  .code>a {
    display: inline-block;
    width: 1.6rem;
    height: 0.48rem;
    font-size: 0.24rem;
    color: #ff6f00;
    border: 1px solid #ff6f00;
    border-radius: 2px;
    text-align: center;
    line-height: 0.48rem;
    position: absolute;
    right: 0.3rem;
    bottom: 0.2rem;
  }
  .address {
    height: 100vh;
    background-color: #F2F2F2;
  }
  .site {
    padding: 0 0.2rem;
    /*display: block;*/
    height: 0.88rem;
    line-height: 0.88rem;
    background-color: #FFFFFF;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #F2F2F2;
  }
  .site span {
    display: inline-block;
    font-size: 0.32rem;
    color: #000000;
    width: 1.4rem;
  }
  .site input {
    flex: 1;
    display: inline-block;
    height: 0.8rem;
    text-indent: 0.5rem;
  }
  .site p{
    padding-left: 0.5rem;
    flex: 1;
    font-size: 0.32rem;
    color: #B2B2B2;
  }
  .site input::placeholder {
    font-size: 0.32rem;
    color: #B2B2B2;
  }
  .code img {
    width: 0.39rem;
  }
  .footer_btn {
    height: 0.88rem;
    background-color: #ff6f00;
    text-align: center;
    line-height: 0.88rem;
    font-size: 0.36rem;
    color: #FFFFFF;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
  }
  .footer_d{
    background-color: #b6b6b6;
  }
</style>



